// Vue  
import Vue from 'vue';

// Vuex  
import Vuex from 'vuex';

// Vuex-persistedstate  
import createPersistedState from "vuex-persistedstate";

//   
import DashboardPlugin from './plugins/dashboard-plugin';

//  App.vue  
import App from './App.vue';

//   
import router from './routes/router';

//  
Vue.use(DashboardPlugin);
Vue.use(Vuex);

// Vuex  - Store: (state[ Data], mutations[-], actions[-], getter[])
const store = new Vuex.Store({
  //     , Vuex     
  plugins: [
      createPersistedState()
  ],
  //    ( -   mutations  .)
  state: {
      projects: null
  },
  //      (commit)
  mutations: {
      // logic
  },
  // mutations      . (dispatch)
  actions: {
      // logic
  },
  // computed  . state , view   .
  getters: {
      // logic
  }
});

//   Vue 
new Vue({
  el: '#app',
  store,
  render: h => h(App),
  router
});







